import { Dropdown, Form, Input, Menu, Select, Table, Tabs } from "antd"
import { DownOutlined } from "@ant-design/icons"
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { useDispatch } from "react-redux"
import { initLayoutTab } from "../stores/layoutService"

const dropdownMenuData = [
  { label: "", key: "option1" },
  { label: "", key: "option2" },
]
const { Option } = Select
const Page1 = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [menu, setMenu] = useState<{
    label: string
    key: string
  }>()

  const handleDetailLink = (val: string) => {
    const encParams = btoa(JSON.stringify({ userId: val }))
    // tab
    // navigate(`/page1/${encParams}`, { replace: true })
    dispatch(
      initLayoutTab({
        menu: { label: "1", key: `/page1/${encParams}` },
        navigate,
      })
    )
  }
  return (
    <Tabs>
      <Tabs.TabPane tab="1tab1" key="/tab1">
        <Dropdown
          trigger={["click"]}
          overlay={
            <Menu
              onClick={(info) => {
                setMenu(dropdownMenuData.find((m) => m.key === info.key))
              }}
            >
              {dropdownMenuData.map((menu) => (
                <Menu.Item key={menu.key}>{menu.label}</Menu.Item>
              ))}
            </Menu>
          }
        >
          <span style={{ cursor: "pointer" }}>
            {menu?.label || ""} <DownOutlined />
          </span>
        </Dropdown>
        <Form>
          <Form.Item label="tab11" name="username1">
            <Input />
          </Form.Item>
          <Form.Item label="tab12" name="gender1">
            <Select allowClear>
              <Option value="male">male2</Option>
              <Option value="female">female2</Option>
              <Option value="other">other2</Option>
            </Select>
          </Form.Item>
        </Form>

        <Table
          rowKey="userId"
          columns={[
            {
              dataIndex: "userId",
              title: "",
              render: (val) => (
                <span
                  style={{ color: "blue", cursor: "pointer" }}
                  onClick={() => handleDetailLink(val)}
                >
                  {val}
                </span>
              ),
            },
            { dataIndex: "userName", title: "" },
          ]}
          dataSource={[
            { userId: "123", userName: "xx" },
            { userId: "456", userName: "xx" },
          ]}
        />
      </Tabs.TabPane>
      <Tabs.TabPane tab="1tab2" key="/tab2">
        <Form>
          <Form.Item label="tab21" name="username2">
            <Input />
          </Form.Item>
          <Form.Item label="tab22" name="gender2">
            <Select allowClear>
              <Option value="male">male2</Option>
              <Option value="female">female2</Option>
              <Option value="other">other2</Option>
            </Select>
          </Form.Item>
        </Form>

        <Table
          rowKey="userId"
          columns={[
            {
              dataIndex: "userId",
              title: "",
              render: (val) => (
                <span
                  style={{ color: "blue", cursor: "pointer" }}
                  onClick={() => handleDetailLink(val)}
                >
                  {val}
                </span>
              ),
            },
            { dataIndex: "userName", title: "" },
          ]}
          dataSource={[{ userId: "789", userName: "xx" }]}
        />
      </Tabs.TabPane>
    </Tabs>
  )
}

export default Page1
